	module.exports = {"title":"Introduction","body":"<h1>The Architecture Of A Cryptocurrency</h1>\n<p>Though we have thousands cryptocurrencies buzzing around at the moment, all of them share the same\nimplementation design. Basically every cryptocurrency implementation has following parts:</p>\n<ul>\n<li>Network Protocol</li>\n<li>Consensus Protocol</li>\n<li>Transaction Protocol</li>\n<li>Internal State</li>\n</ul>\n<h2>Network Protocol</h2>\n<p>P2P network is the core of a cryptocurrency, and the core is following some network protocol. It is\npretty simple usually. For example, p2p network of a cryptocurency could be implemented with\nasynchronous exchange of the following messages:</p>\n<ul>\n<li>M1: Serialized unconfirmed transaction</li>\n<li>M2: Serialized block</li>\n<li>M3: Blockchain quality score request(e.g. height for the Bitcoin, cumulative difficulty for the Nxt).</li>\n<li>M4: Blockchain quality score response</li>\n<li>M5: Get known peers request</li>\n<li>M6: Get known peers response</li>\n<li>M7: Request a block for a certain height(or with a certain parent)</li>\n</ul>\n<p>The minimalistic set is pretty similar to Scorex message types. Bitcoin has more complicated protocol\nfor sure(<a href=\"https://en.bitcoin.it/wiki/Protocol_documentation#Message_types\">https://en.bitcoin.it/wiki/Protocol_documentation#Message_types</a>)\nas well as NXT.</p>\n<p>Lets consider an example of a blockchain downloading with messages types given above if peer A knows only peer B and\nhas genesis block only. So possible trace of interaction of A and outer world could be as follows (where\nA -&gt; (B, M2) means (peer A sends an instance of message kind M2 to peer B)):</p>\n<ol>\n<li>A asks B for peers: A -&gt; (B, M5)</li>\n<li>A asks B for a Bs blockchain height: A -&gt; (B, M3)</li>\n<li>B replies for the first message with known peers C &amp; D: B -&gt; (A, M6)</li>\n<li>B replies for the second message with its height H(B): B -&gt; (A, M4)</li>\n<li>A asks C for its blockchain height: A -&gt; (C, M3)</li>\n<li>A asks D for its blockchain height: A -&gt; (D, M3)</li>\n<li>C replies with its blockchain height H: C -&gt; (A, M4)</li>\n<li>D replies with its blockchain height H(D): D -&gt; (A, M4)</li>\n</ol>\n<p>So A have 3 known heights H(B), H, H(D) and a bigger height means better chain(for a Proof-of-Work\ncryptocurrency), so A chooses peer with a best chain, e.g. D. Then its going to download a chain from D\nwith a following cycle:</p>\n<ol start=\"9\">\n<li>A asks D for a block with height (2,3,,H(D)): A -&gt; (D, M7)</li>\n<li>D replies with a block requested serialized into a binary form: D -&gt; (A, M2)</li>\n</ol>\n<p>Any real trace is much more complicated than this example :)</p>\n<h2>Consensus Protocol</h2>\n<p>By using network protocol a node can download one chain or another or even multiple chains (as different\npeers can have different chains). Then the question is what is the canonical chain shared by majority, the\nright kind of history an user can rely on. Consensus protocol aims to solve the problem.</p>\n<p>A node follow rules to determine canonical chain like these:</p>\n<ol>\n<li>\n<p>Every block in a chain must be valid as well as its signature</p>\n</li>\n<li>\n<p>Every block must have a valid reference to its parent with first block having reference\nto a genesis block which is constant for an each node</p>\n</li>\n<li>\n<p>Every block must be generated by a party having a right to produce it</p>\n</li>\n<li>\n<p>From multiple chains, one having maximum score is the canonical. If few chains have the\nsame score first seen or a random one could be chosen</p>\n</li>\n</ol>\n<p>Some notes about the rules:</p>\n<ul>\n<li>\n<p>Block validity fact could be changed with a new software release. Most known example of that is\nBitcoin network splitting caused by 0.8 release</p>\n</li>\n<li>\n<p>In proof-of-work cryptocurrencies a party has a right to generate block if hash of its contents\nconforms to some condition(its cheap to calculate a hash and compare it with some value while its hard\nto iterate over nonce bytes to find such a hash). In proof-of-stake cryptocurrencies a party has verifiable\nright to generate a block if some pseudo-random value <code>hit</code> is less than stake-dependent <code>target</code>.\nIts harder to design safe Proof-of-Stake cryptocurrency, for example, majority of such currencies are\nvulnerable to grinding attack(Nxt is not).</p>\n</li>\n<li>\n<p>In Bitcoin longest chain rule being applied while in Nxt theres another blockchain score function\ncalled cumulative difficulty</p>\n</li>\n<li>\n<p>More details on the Nxt algo: <a href=\"http://chepurnoy.org/blog/2014/10/inside-a-proof-of-stake-cryptocurrency-part-2/\">http://chepurnoy.org/blog/2014/10/inside-a-proof-of-stake-cryptocurrency-part-2/</a></p>\n</li>\n</ul>\n<h2>Transaction Protocol</h2>\n<p>A block contains transactions. A transaction is the global state modifier. In simplest case it modifies\nbalance sheet. As theres no central party, each node need to have the same state as others(for the same block height).\nSo each node executes all the transactions coming within blocks, and validity rules must be same for each network participant.</p>\n<p>A transaction could be implemented in different ways depends on goals:</p>\n<ul>\n<li>\n<p>In Bitcoin, transaction has multiple inputs and outputs with a script attached to every pin.</p>\n</li>\n<li>\n<p>Ethereum has two transaction types, contract creation and message call. See the Yellow Paper for details:\n<a href=\"http://gavwood.com/Paper.pdf\">http://gavwood.com/Paper.pdf</a>.</p>\n</li>\n<li>\n<p>In Nxt, transaction has some money amount and attachments. There are some rules on attachments processing,\ne.g. some kind of attachment means plain or encrypted message while other means order on\ndecentralized exchange. Such an implementation provides maximum performance for features but has own\ndisadvantages(complicated code, hardforks on each software update).</p>\n</li>\n</ul>\n<h2>Internal State</h2>\n<p>In Bitcoin node store no any state at all or use different formats for it, e.g. UTXO list or some indexes in addition to that.\nIn contrast, Ethereum has state hash stored into each block, so each node must comply with some standard state representation\ninterface given in the Yellow Paper to check block validity. Theres also interesting proposal from Bill White(coming\nalong with Coq theory) about using special kind of Merkle tree and storing its root into block for scalabilitys sake:\n[<a href=\"http://qeditas.org/gitweb/?p=ledgertheory.git;a=blob_plain;f=lightcrypto.pdf;hb=HEAD\">http://qeditas.org/gitweb/?p=ledgertheory.git;a=blob_plain;f=lightcrypto.pdf;hb=HEAD</a>]\n(<a href=\"http://qeditas.org/gitweb/?p=ledgertheory.git;a=blob_plain;f=lightcrypto.pdf;hb=HEAD\">http://qeditas.org/gitweb/?p=ledgertheory.git;a=blob_plain;f=lightcrypto.pdf;hb=HEAD</a>).</p>\n<h2>The Protocols Mess Problem</h2>\n<p>The real problem of cryptocurrency development process is the mess of all the parts described above.\nThe most known example, even simple change in software validation(so in Transaction Protocol) could causes\nhardforking.</p>\n<h2>Acknowledgments</h2>\n<p>The idea of Network/Transaction/Consensus protocols was initially explicitly stated by L.M Goodman in the\n&quot;Tezos: A Self-Amending Crypto-Ledger Position Paper&quot; (<a href=\"http://tezos.com/position_paper.pdf\">http://tezos.com/position_paper.pdf</a>).\nI would like to thank lead Nxt developer Jean-Luc Picard for conversations via dev maillist, and Bill White\nfor very thoughtful conversations.</p>\n<h2>Following Articles</h2>\n<ul>\n<li><a href=\"modular1.md\">On the Way to a Modular Cryptocurrency, Part 1: Generic Block Structure</a></li>\n</ul>\n"}

