	module.exports = {"title":"Settings","body":"<h1>Network settings</h1>\n<p>Network-wide settings are compiled with the source code and can be managed with <code>application.conf</code> file via <a href=\"https://github.com/typesafehub/config\">typesafe config library</a>.</p>\n<pre><code>app {\n  # Release name. Will be used by peers <span class=\"hljs-keyword\">to</span> identify the network\n  release = <span class=\"hljs-string\">\"Lagonaki\"</span>\n\n}\nscorex {\n  # Full path <span class=\"hljs-keyword\">to</span> the main hash <span class=\"hljs-keyword\">function</span>. It should implement CryptographicHash <span class=\"hljs-keyword\">interface</span>. You can find a lot <span class=\"hljs-keyword\">of</span> hash functions <span class=\"hljs-keyword\">in</span> scrypto library (https:<span class=\"hljs-comment\">//github.com/ScorexProject/scrypto).</span>\n  fastHash = <span class=\"hljs-string\">\"scorex.crypto.hash.Blake256\"</span>\n\n  # Full path <span class=\"hljs-keyword\">to</span> slow <span class=\"hljs-keyword\">and</span> most secure hash <span class=\"hljs-keyword\">function</span>. It should implement CryptographicHash <span class=\"hljs-keyword\">interface</span>. You can find a lot <span class=\"hljs-keyword\">of</span> hash functions <span class=\"hljs-keyword\">in</span> scrypto library (https:<span class=\"hljs-comment\">//github.com/ScorexProject/scrypto).</span>\n  secureHash = <span class=\"hljs-string\">\"scorex.crypto.hash.ScorexHashChain\"</span>\n}\n</code></pre>\n<h1>Node settings</h1>\n<p>To run your Scorex node you should create json file with application settings. Most of settings have default values so you may provide just few settings to customize your app.</p>\n<h2>Scorex-basics settings</h2>\n<table>\n<thead>\n<tr>\n<th>Parameter</th>\n<th>Default value</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>dataDir</code></td>\n<td>undefined</td>\n<td>Optional folder to keep your data. When undefined, all the data is kept in memory</td>\n</tr>\n<tr>\n<td><code>localOnly</code></td>\n<td><code>false</code></td>\n<td>Node is only available locally</td>\n</tr>\n<tr>\n<td><code>rpcAddress</code></td>\n<td><code>0.0.0.0</code></td>\n<td>Bind address for remote procedure call</td>\n</tr>\n<tr>\n<td><code>rpcPort</code></td>\n<td><code>9085</code></td>\n<td>Port for remote procedure call</td>\n</tr>\n<tr>\n<td><code>rpcAllowed</code></td>\n<td><code>127.0.0.1</code></td>\n<td>Space separated list of addresses, that can use rpc</td>\n</tr>\n<tr>\n<td><code>offlineGeneration</code></td>\n<td><code>false</code></td>\n<td>Allow node to generate new blocks when its offline</td>\n</tr>\n<tr>\n<td><code>historySynchronizerTimeout</code></td>\n<td><code>30</code></td>\n<td>Timeout in seconds for the history synchronizer to wait a response from remote nodes</td>\n</tr>\n<tr>\n<td><code>blockGenerationDelay</code></td>\n<td><code>1000</code></td>\n<td>Time in milliseconds for block generator to try to generate a new block</td>\n</tr>\n<tr>\n<td><code>mininigThreads</code></td>\n<td><code>1</code></td>\n<td>Number of threads for mining</td>\n</tr>\n<tr>\n<td><code>walletDirOpt</code></td>\n<td>undefined</td>\n<td>Optional folder to keep your wallet. When undefined, your wallet is kept in memory</td>\n</tr>\n<tr>\n<td><code>walletPassword</code></td>\n<td>undefined</td>\n<td>Password for your wallet. When undefined, you should pass it on application start</td>\n</tr>\n<tr>\n<td><code>walletSeed</code></td>\n<td>undefined</td>\n<td>Seed for your wallet to generate new accounts. When undefined, you should pass it on application start</td>\n</tr>\n<tr>\n<td><code>apiKeyHash</code></td>\n<td>undefined</td>\n<td>Hash for API key. When undefined, RPC requests are allowed without any authentication</td>\n</tr>\n<tr>\n<td><code>genesisTimestamp</code></td>\n<td><code>1460952000000</code></td>\n<td>Timestamp for genesis block.</td>\n</tr>\n<tr>\n<td><code>cors</code></td>\n<td><code>false</code></td>\n<td>Allow API to called from another domain outside the domain from which the resource originated.</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>nodeName</code></td>\n<td>Random string</td>\n<td>Name of the node</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>bindAddress</code></td>\n<td><code>127.0.0.1</code></td>\n<td>The socket address to bind to</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>port</code></td>\n<td><code>9084</code></td>\n<td>Port of the socket address to bind to</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>maxConnections</code></td>\n<td><code>20</code></td>\n<td>Maximum number of outgoing connections</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>connectionTimeout</code></td>\n<td><code>60</code></td>\n<td>Connection timeout in seconds</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>upnp</code></td>\n<td><code>true</code></td>\n<td>Enable upnp</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>upnpGatewayTimeout</code></td>\n<td><code>true</code></td>\n<td>The timeout for actions on the device</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>upnpDiscoverTimeout</code></td>\n<td><code>true</code></td>\n<td>The timeout for socket connections of the initial broadcast request</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>myAddress</code></td>\n<td><code>None</code></td>\n<td>Optional setting to configure declared address</td>\n</tr>\n<tr>\n<td><code>p2p</code>:<code>knownPeers</code></td>\n<td><code>[]</code></td>\n<td>List of known peers</td>\n</tr>\n</tbody>\n</table>\n<h2>Scorex-transaciton settings</h2>\n<table>\n<thead>\n<tr>\n<th>Parameter</th>\n<th>Default value</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>history</code></td>\n<td><code>blockchain</code></td>\n<td>History type. Blockchain or blocktree (experimental)</td>\n</tr>\n<tr>\n<td><code>maxRollback</code></td>\n<td><code>100</code></td>\n<td>Maximum number of blocks to rollback</td>\n</tr>\n</tbody>\n</table>\n<h2>Scorex-perma settings</h2>\n<table>\n<thead>\n<tr>\n<th>Parameter</th>\n<th>Default value</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>perma</code>:<code>rootHash</code></td>\n<td><code>13uSUANW...</code></td>\n<td>Base58-encoded root hash of Merkle tree with permacoin data.</td>\n</tr>\n<tr>\n<td><code>perma</code>:<code>isTrustedDealer</code></td>\n<td><code>false</code></td>\n<td>Set current node as trusted dealer</td>\n</tr>\n<tr>\n<td><code>perma</code>:<code>treeDir</code></td>\n<td>required</td>\n<td>Folder to keep permacoin data</td>\n</tr>\n<tr>\n<td><code>perma</code>:<code>authDataStorage</code></td>\n<td><code>authDataStorage.db</code></td>\n<td>Name of file to put authenticated permacoin data. File will be created in treeDir</td>\n</tr>\n</tbody>\n</table>\n<h2>Settings example</h2>\n<p>Typical json files with settings looks like</p>\n<pre><code class=\"language-json\">{\n  <span class=\"hljs-attr\">\"p2p\"</span>: {\n    <span class=\"hljs-attr\">\"bindAddress\"</span>: <span class=\"hljs-string\">\"0.0.0.0\"</span>,\n    <span class=\"hljs-attr\">\"upnp\"</span>: <span class=\"hljs-literal\">false</span>,\n    <span class=\"hljs-attr\">\"upnpGatewayTimeout\"</span>: <span class=\"hljs-number\">7000</span>,\n    <span class=\"hljs-attr\">\"upnpDiscoverTimeout\"</span>: <span class=\"hljs-number\">3000</span>,\n    <span class=\"hljs-attr\">\"port\"</span>: <span class=\"hljs-number\">9084</span>,\n    <span class=\"hljs-attr\">\"knownPeers\"</span>: [\n      <span class=\"hljs-string\">\"23.94.190.226:9185\"</span>\n    ],\n    <span class=\"hljs-attr\">\"maxConnections\"</span>: <span class=\"hljs-number\">10</span>\n  },\n  <span class=\"hljs-attr\">\"walletDir\"</span>: <span class=\"hljs-string\">\"/tmp/scorex/wallet\"</span>,\n  <span class=\"hljs-attr\">\"dataDir\"</span>: <span class=\"hljs-string\">\"/tmp/scorex/data\"</span>,\n  <span class=\"hljs-attr\">\"rpcPort\"</span>: <span class=\"hljs-number\">9085</span>,\n  <span class=\"hljs-attr\">\"rpcAllowed\"</span>: [],\n  <span class=\"hljs-attr\">\"maxRollback\"</span>: <span class=\"hljs-number\">100</span>,\n  <span class=\"hljs-attr\">\"blockGenerationDelay\"</span>: <span class=\"hljs-number\">0</span>,\n  <span class=\"hljs-attr\">\"genesisTimestamp\"</span>: <span class=\"hljs-number\">1460952000000</span>,\n  <span class=\"hljs-attr\">\"apiKeyHash\"</span>: <span class=\"hljs-string\">\"GmVvcpx1BRUPDZiADbZ7a6zgQV3Sgj2GhNoEiTH9Drdx\"</span>,\n  <span class=\"hljs-attr\">\"cors\"</span>: <span class=\"hljs-literal\">false</span>\n}\n</code></pre>\n"}

