	module.exports = {"title":"Scorex - Modular blockchain framework. Public domain.","body":"<p><a href=\"http://23.94.190.226:8080/job/scorex/branch/master\"><img src=\"http://23.94.190.226:8080/buildStatus/icon?job=scorex/master\" alt=\"Build Status\"></a> <a href=\"https://gitter.im/input-output-hk/Scorex?utm_source=badge&amp;utm_medium=badge&amp;utm_campaign=pr-badge&amp;utm_content=badge\"><img src=\"https://badges.gitter.im/input-output-hk/Scorex.svg\" alt=\"Join the chat at https://gitter.im/input-output-hk/Scorex\"></a></p>\n<h2>Motivation</h2>\n<p>There are two huge problems around cryptocurrencies development project Scorex aims to weaken:</p>\n<ul>\n<li>\n<p>Bitcoin Core source code contains more 100K lines of code(80K of C++ only), Nxt is more than 45K\nline of Java code. All parts of the design(network/transactional/consensus protocols) are mixed in a hard way.\nSo researchers and developers are not in good start positions to make experiments. \\</p>\n<p>In opposite, Scorex is less than 4K lines of Scala code. Transactional layer is as simple as that. Consensus algo\ncould be switched easily(with two consensus algos out of the box, one could be replaced with an another with\njust one line of code edited!)</p>\n</li>\n<li>\n<p>New coins are trying to make money immediately, often having just one or two pretty controversial\nfeature. Scorex is the free and open tool aiming to make other systems development easier.</p>\n</li>\n</ul>\n<h2>Features</h2>\n<ul>\n<li>Compact, functional code</li>\n<li>Modular design</li>\n<li>Scala language</li>\n<li>Two 100% Proof-of-Stake consensus algos out of the box, Nxt-like and Qora-like. One algo could be replaced\nwith an another with just one line of code edited.</li>\n<li>Additional consensus module Proof-of-Work consensus algo is available as <a href=\"https://github.com/ScorexProject/Permacoin-consensus\">separate module</a></li>\n<li>Simplest transactional model</li>\n<li>Asynchronous network layer on top of TCP</li>\n<li>JSON API</li>\n<li>Command line client for the JSON API</li>\n<li>Cryptographic primitives externalized into <a href=\"https://github.com/ScorexProject/scrypto\">separate scrypto framework</a></li>\n</ul>\n<h2>Getting Started</h2>\n<ul>\n<li><a href=\"https://github.com/ScorexProject/Scorex/wiki/Getting-started\">Quick start guide</a></li>\n<li><a href=\"https://github.com/ScorexProject/Scorex/wiki\">Documentation</a></li>\n<li><a href=\"https://github.com/ScorexProject/Lagonaki\">Example project</a></li>\n<li><a href=\"https://github.com/ScorexProject/Permacoin-consensus\">Additional consensus module</a></li>\n<li><a href=\"http://23.94.190.226:8080/job/scorex/\">CI</a></li>\n<li><a href=\"https://github.com/ScorexProject/Scorex/releases\">Releases</a></li>\n</ul>\n<h2>Command-Line Client</h2>\n<p>Run ./cli.sh after launching server to issue API requests to it via command-line client. See API section below.\nSome examples of CLI commands:</p>\n<ul>\n<li>GET blocks/first</li>\n<li>POST payment {amount:400, fee:1, sender:2kx3DyWJpYYfLErWpRMLHwkL1ZGyKHAPNKr,recipient:Y2BXLjiAhPUMSo8iBbDEhv81VwKnytTXsH}</li>\n</ul>\n<h2>More reading</h2>\n<p>Besides of <a href=\"https://github.com/ScorexProject/Scorex/wiki\">documentation</a> there are other resources describing Scorex:</p>\n<p>Articles:</p>\n<p><a href=\"docs/articles/components.md\">The Architecture Of A Cryptocurrency</a></p>\n<p><a href=\"docs/articles/modular1.md\">On the Way to a Modular Cryptocurrency, Part 1: Generic Block Structure</a></p>\n<p><a href=\"docs/articles/modular2.md\">On the Way to a Modular Cryptocurrency, Part 2: Stackable API</a></p>\n<p><a href=\"docs/artices/private-chains.md\">On Private Blockchains, Technically</a></p>\n<p>Readmes:</p>\n<p><a href=\"scorex-basics/README.md\">Scorex-Basics Sub-Module</a></p>\n<p>Others:</p>\n<p><a href=\"docs/API.md\">API Description</a></p>\n<p>Please join our mail-list: <a href=\"https://groups.io/g/scorex-dev\">https://groups.io/g/scorex-dev</a> .</p>\n<h2>Contributions</h2>\n<p>Contributions are welcome! Please take a look into <a href=\"https://github.com/ConsensusResearch/Scorex-Lagonaki/issues\">issues</a>.\nTesting codebase is very small at the moment, so writing a test is not just good for start, but useful as well.</p>\n<h2>License</h2>\n<p>To the extent possible under law, the authors have dedicated all copyright and related and neighboring\nrights to this software to the public domain worldwide. This software is distributed without any warranty.\nYou can find applied CC0 license legalcode in the <a href=\"COPYING\">COPYING</a></p>\n"}

